<template>
  <section class="container">
    <!-- 主页介绍 -->
    <div class="tutor space" v-if="outline">
      <el-row :gutter="10">
        <el-col :xs="24" :sm="24" :md="14" :lg="14" :xl="14" class="df dfdir dfjbw courseInfo">
          <div class="basicInfo">
            <p class="name">{{outline.info.name}}</p>
          </div>
          <div class="contact df dfjbw dfc">
            <div class="touch df dfc">
              <div class="learn">所属品类：
                <span v-for="(relation,i) in outline.types" :key="relation+i">
                  {{relation.catalog_type_name}}
                </span>
              </div>
              <p class="learn">课程时长：{{outline.info.course_duration}}</p>
              <p class="learn">授课讲师：{{teacherInfo.list && teacherInfo.list.map(item => item.name).join("，")}}</p>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>

    <div class="modular space" v-if="outline">
      <el-row :gutter="20">
        <el-col :xs="24" :sm="24" :md="18" :lg="18" :xl="18">
          <div class="vessels">
            <Modular :modeInfo="baseInfo" v-if="baseInfo.content" type="1"/>
            <Modular :modeInfo="teacherInfo" v-if="teacherInfo.list&&teacherInfo.list.length>0" type="4"/>

            <Modular :modeInfo="syllabusInfo" v-if="syllabusInfo.content" type="1"/>
            <Modular :modeInfo="suggestCrowdInfo" v-if="suggestCrowdInfo.list&&suggestCrowdInfo.list.length>0"
                     type="5"/>
          </div>
         <!-- <div class="position"></div> -->
        </el-col>
        <!--<el-col :xs="24" :sm="24" :md="6" :lg="6" :xl="6">-->
        <!--<h3 class="title">推荐老师</h3>-->
        <!--<div class="tList">-->
        <!--<div class="character">-->
        <!--<p class="photoShow"-->
        <!--style='backgroundImage:url("https://sslfile.ichazuo.cn/1480920120bb1443cc31d7396bf73e7858cea114e1.jpg")'></p>-->
        <!--<div class="desc">-->
        <!--<p class="name">-->
        <!--倪建伟-->
        <!--<span v-if="true">销售类</span></p>-->
        <!--<div class="ellipsis2 info">-->
        <!--<p>服务过华为、海尔、腾讯、 苏宁、中国移动等100+企业</p>-->
        <!--</div>-->
        <!--</div>-->
        <!--</div>-->
        <!--</div>-->
        <!--</el-col>-->
      </el-row>
    </div>
  </section>
</template>
<script>
  import {mapState, mapActions} from 'vuex'
  import {host} from '@/assets/js/global'
  import merge from 'webpack-merge'
  import Modular from '../../../components/Modular'

  export default {
    data() {
      return {
        navIndex: 0,
        baseInfo: {},
        teacherInfo: {},

        syllabusInfo: {},
        suggestCrowdInfo: {}
      }
    },
    computed: {
      ...mapState({
        outline: state => state.course.outline.data
      })
    },
    created() {
      const params = this.$route.params
      const catalogId = params.id
      this.getOfflineCourseById(catalogId)
      document.title = `${this.$route.name} - 插座学院`
    },
    methods: {
      ...mapActions({
        getOutlineInfo: 'course/getTeachersList',
      }),
      /**
       *  获取课程大纲
       */
      async getOfflineCourseById(catalogId) {
        const res = await this.getOutlineInfo({catalogId: catalogId})
        const {info={}, studentLabels=[], teachers=[], types} = res.data;
        this.baseInfo = {
          title: "项目概述",
          content: info.catalog_description
        }
        this.teacherInfo = {
          title: "授课讲师",
          list: teachers
        }

        this.syllabusInfo = {
          title: "课程大纲",
          content: info.catalog_outline
        }
        this.suggestCrowdInfo = {
          title: "建议学习人群",
          list: studentLabels
        }
      },


      /**
       * 路由跳转
       * @param path 路由地址
       */
      toPath(path) {
        this.$router.push(path)
      },
    },
    components: {
      Modular
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .container {
    width: 75%;
    margin: 0 auto;
    overflow: hidden;
    min-height: 100vh;
    margin-bottom: -295px;
    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 380px;
    }

    .space {
      margin-top: 24px;
    }

    .tutor {
      background-color: #fff;
      padding: 40px;
      .courseInfo {
        .basicInfo {
          .name {
            font-size: 40px;
            font-weight: bold;
            color: #333;
            margin-bottom: 20px;
          }
          .tags {
            overflow: hidden;
            margin: 14px -8px;
            span {
              float: left;
              margin: 8px;
              padding: 0 16px;
              line-height: 36px;
              font-size: 14px;
              color: #06609A;
              border: 1px solid #E6EFF4;
              border-radius: 18px;
            }
          }
        }

        .contact {
          .enroll {
            width: 188px;
            line-height: 64px;
            text-align: center;
            background: #06609A;
            border-radius: 32px;
            color: #fff;
            font-size: 24px;
          }

          .touch {
            font-size: 16px;
            line-height: 1.6;
            color: #777;
            .learn {
              margin-right: 60px;
              span {
                display: inline-block;
                font-size: 18px;
                color: #80624C;
                padding: 4px 6px;
                background-color: #E2AD87;
                border-radius: 2px;
              }
            }
          }
        }
      }
    }

    .modular {
      .vessels {
        background: #fff;
        // padding-bottom: 80px;
      }

      // .position {
      //   background: #fff;
      //   height: 80px;
      // }

      .title {
        font-size: 24px;
        color: #666;
        margin: 16px 0 12px;
      }
      .tList {
        .character {
          padding: 20px;
          background: #fff;
          box-shadow: 3px 4px 8px #f7f7f7;
          position: relative;
          overflow: hidden;
          cursor: pointer;
          border-radius: 6px;
          .photoShow {
            width: 100%;
            height: 218px;
            display: block;
            border-radius: 8px;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
          }
          .desc {
            width: 100%;
            background: #fff;
            padding: 24px 20px;
            box-sizing: border-box;
            .name {
              font-size: 24px;
              color: #06609A;
              span {
                padding: 2px 6px;
                margin-left: 8px;
                background: #E2AD87;
                color: #80624C;
                border-radius: 4px;
                font-size: 12px;
              }
            }
            .info {
              font-size: 14px;
              color: #666;
              line-height: 2;
              margin-top: 8px;
              .goodAt {
                margin-top: 10px;
              }
            }
          }
        }
      }

    }
  }

  .page {
    padding: 32px 0;
    text-align: center;
  }

  @media screen and (min-width: 200px) and (max-width: 768px) {
    .container {
      box-sizing: border-box;
      display: flex;
      width: 100%;
      overflow: hidden;
      flex-direction: column;
    }

    .sidebar {
      box-sizing: border-box;
      width: auto;
      flex: 1;
      margin-left: 0;
      margin-top: 24px;
    }
  }
</style>



